// 清除浮動
.clearfix {
    @include clearfix;
}


// 全站設定
* {
    box-sizing: border-box;
    @include selection;
}

html,
body {
    margin: 0;
    padding: 0;
}

input, 
select, 
textarea, 
button, 
a {
    outline: none;
    font-family: 'Questrial','微軟正黑體', sans-serif;
}

a {
    text-decoration: none;
}

input, 
select, 
textarea, 
button {
    background-color: transparent;
    border: 0;
}

button {
    cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: normal;
    margin: 0;
    padding: 0;
}

ul,
ol,
figure,
p {
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Questrial','微軟正黑體', sans-serif;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
    @include scrollbar($first);
    padding-left: 15rem;
}

main {
    position: relative;
    min-height: calc(100vh - 3rem);
    padding: 2rem;
}